@import '../Default.scss';

.container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-flow: column;

    transition: $transition;
}

.White{
    background: $lightGrey;
}

.Black{
    background: $darkGrey;
}

.Horizontal{
    display: none;
    width: inherit;
    height: inherit;
    z-index: 1;

    background: $blue;
    color: $white;

    position: fixed;
    top: 0;
    left: 0;
    
    padding: $padding;
}

.Message{
    width: inherit;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.Header{
    font-size: $small;
    font-weight: bold;
    margin-bottom: 1rem;
}

.Subheader{
    font-size: 1rem;
    font-weight: normal;
}

@media only screen and (min-width: 426px) {
    .container{
        height: 100%;
    }
}

@media screen and (orientation: landscape) and (max-width: 832px) and (max-height: 426px) {
    .container{ display: none; }
    .Horizontal { display: block; }
  }