@import '../../Default.scss';

.Display{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    
    font-weight: normal;
    text-align: right;
    
    padding: $extraSmall $padding;
    transition: $transition;

    flex: 1 1 auto;
}

.Black{
    color: $black;
}

.White{
    color: $white;
}

.Result{
    font-size: $extraSmall;
    color: $grey;
}

.Total{
    font-size: $large;
}

.TextSmall{
    font-size: $medium;
}

.TextSmaller{
    font-size: $small;
}

.TextSmallest{
    font-size: 1rem;
}