@import '../../Default.scss';

.Header{
    @extend %flex-center-spaceBetween;

    padding: $padding;
}

.Button{
    color: $grey;
    transition: $transition;
    
    i{
        font-size: 1rem;
    }
}

.HistoryHeader{
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-size: 1rem;
    text-decoration: underline;
    color: $grey;
}

.Text{
    font-size: 1rem;
    color: $grey;
}

.Hidden{
    display: none;
}