@import '../../Default.scss';

.Button{
    flex: 1;
    @extend %flex-center-spaceBetween;
    justify-content: center;

    font: inherit;
    font-size: $small;
    border-radius: $borderRadius;

    margin: 0 0.25rem;
    padding: 1rem 0;

    transition: $transition;

    i{
        font-size: $small;
    }

    &:focus{
        background: $grey;
    }
}

.Wide{
    flex: 2;
}

.Red{
    color: $red;
}

.Green{
    color: $green;
    background: $green;
}

.LightGrey{
    background: $lightGrey;
}

.DarkGrey{
    background: $darkGrey;
}

.Black{
    color: $black;
}

.White{
    color: $white;
}

.Transparant{
    background: transparent;
}

@media only screen and (min-width: 375px){
    .Button{
        padding: $extraSmall 0;
    }
}

@media only screen and (min-width: 426px){
    .Button{
        padding: $small 0;
    }
}