h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}

button{
  cursor: pointer;
  background: transparent;
  border: none;
}
  
button:hover, button:focus{
  outline: none;
}

html{
  font-size: 16px;
  font-family: 'Roboto Mono', monospace;
}

body{
  font-family: inherit;
}

#root{
  height: auto;
  display: flex;
}


@media only screen and (min-width: 426px) {
  body {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  #root{
    width: 375px;
    height: 667px;
    box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
  }
}

@media only screen and (min-width: 426px) and (min-height: 812px) {
  #root{
    height: 812px;
  }
}

@media screen and (orientation: landscape) and (max-width: 832px) and (max-height: 426px) {
  body, #root{
    width: 100%;
    height: 100vh;
  }
}

