@import '../../Default.scss';

.History{
    height: auto;
    overflow-y: auto;
    @extend %flex-center-spaceBetween;
    flex-direction: column;

    padding: 0 $padding;
}

.Button{
    width: 100%;

    display: block;
    border-radius: $borderRadius;
    background: $red;
    color: $white;
    font-size: $extraSmall;
    font-weight: 600;

    padding: 0.75rem;

    transition: $transition;
}

.Hidden{
    visibility: hidden;
}

.List{
    width: 100%;
    height: auto;
    overflow-y: auto;
    margin: $margin;
    list-style-type: none;

    @extend %flex-center-spaceBetween;
    flex-direction: column;
}

.ListItem{
    display:inline-block;
    @extend %flex-center-spaceBetween;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    margin: 0.5rem;
    text-align: right;
    border-radius: $borderRadius;
    border: 1.5px solid $grey;
    padding: 0.75rem 1rem;
}

.Total{
    color: $blue;
    font-weight: bold;
}

.Calculation{
    color: $grey;
}

.TextSmallest{
    font-size: 1rem;
}

.TextSmaller{
    font-size: $extraSmall;
}

.TextSmall{
    font-size: $small;
}

.TextNormal{
    font-size: $medlow;
}