/* -------------------- Variables -------------------- */
/* --------------- Colors --------------- */
$white: #FAFAFA;
$lighterGrey: #F5F5F5;
$lightGrey: #EEEEEE;
$grey: #9E9E9E;
$darkGrey: #424242;
$black: #212121;
$red: #EF5350;
$green: #66BB6A;
$blue: #42A5F5;

/* --------------- Sizes --------------- */
$extraSmall: 1.25rem;
$small: 1.5rem;
$medlow: 2rem;
$medium: 2.25rem;
$large: 3.375rem;
$extraLarge: 4.25rem;

/* --------------- Margin & Padding --------------- */
$padding: 1.25rem;
$margin: 1.25rem;

/* --------------- Transition --------------- */
$transition: 0.5s ease-in-out;

/* --------------- Border-Radius --------------- */
$borderRadius: 10px;

/* -------------------- Extend/Inheritance -------------------- */
%flex-center-spaceBetween{
  display: flex;
  align-items: center;
  justify-content: space-between;
}