@import '../../Default.scss';

.Keypad{
    padding: $small 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background: $white;

    flex: 0 0 auto;

    transition: $transition;
}

.Row{
    height: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0.25rem 0;
}

.DarkMode{
    background: $black;
}